html {
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font: inherit;
}

body {
  min-height: 100vh;
  background-color: hsl(28deg, 100%, 97%);
  font-size: 1rem;
  line-height: 1.625rem;
  color: hsl(30deg, 5%, 45%);
  font-family: "Plus Jakarta Sans", sans-serif;
}
@media (min-width: 40rem) {
  body {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
}

ul, ol {
  list-style-type: none;
}

img, svg, video, picture {
  display: block;
  max-width: 100%;
}

h1,
h2,
h3 {
  font-weight: 700;
  color: hsl(0deg, 0%, 1%);
}

h1 {
  font-size: 2.25rem;
  line-height: 2.8125rem;
}
@media (min-width: 40rem) {
  h1 {
    font-size: 2.75rem;
    line-height: 3.4375rem;
  }
}
@media (min-width: 59.375rem) {
  h1 {
    font-size: 3.5rem;
    line-height: 4.4375rem;
  }
}

h2 {
  font-size: 1.625rem;
  line-height: 2.0475rem;
}
@media (min-width: 40rem) {
  h2 {
    font-size: 2rem;
    line-height: 2.5rem;
  }
}
@media (min-width: 40rem) {
  h2 {
    font-size: 2.5rem;
    line-height: 3.15rem;
  }
}

h3 {
  font-size: 2rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 74.3125rem;
  margin: 0 auto;
  padding: 0 1rem;
}
@media (min-width: 40rem) {
  .container {
    padding: 0 2.4375rem;
  }
}

.button__cta {
  display: inline-block;
  color: hsl(28deg, 100%, 97%);
  font-size: 0.875rem;
  font-weight: 700;
  padding: 0.5rem 1.75rem;
  border-radius: 1.75rem;
  cursor: pointer;
  transition: all 200ms ease-in-out;
}
@media (min-width: 40rem) {
  .button__cta {
    font-size: 1rem;
    padding: 0.875rem 2.8125rem;
  }
}
.button__cta--1 {
  background: hsl(0deg, 0%, 1%);
}
.button__cta--1:hover {
  background: hsl(252deg, 66%, 62%);
}
.button__cta--2 {
  background: hsl(7deg, 77%, 66%);
}
.button__cta--2:hover {
  background: hsl(28deg, 89%, 67%);
}

.card {
  color: hsl(0deg, 0%, 100%);
  padding: 1.5rem;
  border-radius: 0.5rem;
  font-size: 1.5rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.card img {
  align-self: flex-end;
}

.header {
  margin-top: 1rem;
  margin-bottom: 2rem;
}
@media (min-width: 40rem) {
  .header {
    margin-top: 2.1875rem;
    margin-bottom: 4.0625rem;
  }
}
.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: block;
  width: 3rem;
  height: 3rem;
}
@media (min-width: 40rem) {
  .header__logo {
    width: 4rem;
    height: 4rem;
  }
}

.intro {
  margin-bottom: 6.25rem;
}
@media (min-width: 40rem) {
  .intro {
    margin-bottom: 7.5rem;
  }
}
@media (min-width: 59.375rem) {
  .intro {
    margin-bottom: 8.5rem;
  }
}
.intro__text {
  text-align: center;
}
.intro__title {
  margin-bottom: 1rem;
}
@media (min-width: 40rem) {
  .intro__title {
    margin-bottom: 1.6875rem;
  }
}
@media (min-width: 59.375rem) {
  .intro__title {
    margin-bottom: 1.4375rem;
  }
}
.intro__paragraph {
  margin-bottom: 2rem;
  padding: 0 1.875rem;
}
@media (min-width: 40rem) {
  .intro__paragraph {
    margin-bottom: 4rem;
    padding: 0 3.65625rem;
  }
}
@media (min-width: 59.375rem) {
  .intro__paragraph {
    margin-bottom: 5rem;
    padding: 0 12.5rem;
  }
}
.intro__grid {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: "design design" "uiux apps" "illustration illustration" "photography photography" "motion motion";
}
@media (min-width: 40rem) {
  .intro__grid {
    grid-template-columns: repeat(4, 1fr);
    grid-template-areas: "design design uiux apps" "design design illustration illustration" "photography photography motion motion";
  }
}
@media (min-width: 59.375rem) {
  .intro__grid {
    grid-template-columns: repeat(6, 1fr);
    grid-template-areas: "design design uiux apps photography photography" "design design illustration illustration motion motion";
  }
}
.intro__card:nth-child(1) {
  grid-area: design;
  background: hsl(252deg, 66%, 62%);
  gap: 5.875rem;
}
.intro__card:nth-child(2) {
  grid-area: uiux;
  background: hsl(28deg, 89%, 67%);
  gap: 2.5rem;
}
.intro__card:nth-child(3) {
  grid-area: apps;
  background: hsl(0deg, 78%, 79%);
  gap: 2.5rem;
}
.intro__card:nth-child(4) {
  grid-area: illustration;
  background: hsl(7deg, 77%, 66%);
  gap: 1rem;
}
.intro__card:nth-child(5) {
  grid-area: photography;
  background: hsl(172deg, 46%, 57%);
  gap: 2.5rem;
}
.intro__card:nth-child(6) {
  grid-area: motion;
  background: hsl(314deg, 45%, 23%);
  gap: 1rem;
}

.about {
  text-align: center;
}
.about__picture {
  max-width: 18.75rem;
  margin: 0 auto;
  margin-bottom: 2.5rem;
}
.about__title {
  margin-bottom: 1.5rem;
}